.hotel-about {
  position: relative;
}

.hotel-about__container {
  @include respondTo('diapente') {
    position: relative;
  }
}
  
.hotel-about__tagline {
  margin-bottom: 6px;
  text-align: right;
}

.hotel-about__brand {
  text-align: right;
  margin-left: auto;
  margin-right: -12.5px;

  @include respondTo('pre-unisonus') {
    margin-bottom: 4vw;
  }

  @include respondTo('unisonus') {
    margin-bottom: 16px;
  }

  @include respondTo('diapente') {
    margin-bottom: 24px;
  }

  @include respondTo('diapason') {
    color: #828282;
  }
}

.hotel-about__description {
  margin-left: auto;
  color: mix(getColor('primary-inversed'), getColor('primary'), 37.3%);


  @include respondTo('pre-unisonus') {
    max-width: 58vw;
    margin-bottom: 7vw;
    font-size: 9px;
  }

  @include respondTo('unisonus') {
    max-width: 242px;
    margin-bottom: 28px;
  }

  @include respondTo('diapente') {
    max-width: 419px;
    margin-bottom: 142px;
  }

  @include respondTo('diapason') {
    max-width: 488px;
    margin-bottom: 162px;
  }
}

.hotel-about__interior-first {
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  overflow: hidden;

  @include respondTo('pre-unisonus') {
    max-width: 63%;
    margin-bottom: 32px;
  }

  @include respondTo('unisonus') {
    max-width: 244px;
  }

  @include respondTo('tritonus') {
    margin-bottom: 32px;
  }

  @include respondTo('diapente') {
    box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
    position: absolute;
    top: -144px;
    left: 112px;
    max-width: 400px;
  }

  @include respondTo('diapason') {
    left: 180px;
    max-width: 460px;
  }
}

.hotel-about__wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  @include respondTo('tritonus') {
    margin-bottom: 32px;
  }
}

.hotel-about__descr-second {
  color: #c79002;

  @include respondTo('pre-unisonus') {
    max-width: 42%;
    font-size: 9px;
    margin-right: 5vw;
  }

  @include respondTo('unisonus') {
    max-width: 163px;
    margin-right: 21px;
  }

  @include respondTo('diapente') {
    position: relative;
    top: 7px;
    max-width: 312px;
    margin-right: 48px;
  }

  @include respondTo('diapason') {
    bottom: 40px;
    max-width: 311px;
    margin-right: 45px;
  }
}

.hotel-about__interior-second {
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  z-index: 1;
  overflow: hidden;

  @include respondTo('pre-unisonus') {
    max-width: 48%;
  }

  @include respondTo('unisonus') {
    max-width: 200px;
  }

  @include respondTo('diapente') {
    max-width: 350px;
  }

  @include respondTo('diapason') {
    max-width: 460px;
  }
}

.container-swiper--positioned {

  @include respondTo('diapente') {
    margin-top: -86px;
    margin-left: -120px;
  }

  @include respondTo('diapason') {
    margin-top: -220px;
    margin-left: -88px;
  }

}

.swiper--modified {
  @include respondTo('tritonus') {
    max-width: 312px;
    transform: translateX(28px);
  }

  @include respondTo('diapente') {
    transform: translateX(210px);
  }

  @include respondTo('diapason') {
    transform: translateX(273px);
  }
}

.swiper-slide--modified {
  overflow: hidden;
}

.wrapper-mask > .swiper-button-prev--positioned {

  @include respondTo('tritonus') {
    left: 307px;
  }

  @include respondTo('diapente') {
    left: 391px;
  }

  @include respondTo('diapason') {
    left: 495px;
  }
}

.wrapper-mask > .swiper-button-next--positioned {

  @include respondTo('tritonus') {
    left: 369px;
  }

  @include respondTo('diapente') {
    left: 457px;
  }

  @include respondTo('diapason') {
    left: 556px;
  }
}